<template>
    <div style="height:100%;">
        <div style="background:#fff;">
            <div class="loginNav">
                <img src="../../assets/login/return.jpg" alt="" class="return" @click="returns">
                <p class="title f30 cfff text-center">登录</p>
            </div>
            <img src="../../assets/login/erha.jpg" alt="" class="logo">
            <p class="name f26 c333 mt-40 text-center">智慧校园</p>
            <div class="inpDiv mt-145">
                <img src="../../assets/login/phone.png" alt="" class="icon">
                <input type="text" class="oinp f34 pl-72 cccc" v-model="login.phone" @focus='hide' @blur="shows" placeholder="请输入手机号">
            </div>
            <div class="inpDiv mt-46">
                <img src="../../assets/login/pass.png" alt="" class="icon">
                <input type="password" class="oinp f34 pl-72 cccc" v-model="login.password" @focus='hide' @blur="shows" placeholder="请输入密码">
            </div>
            <div class="lossPsw"><router-link to="/lostPsw/lost" class="ospan f22 cc3">忘记密码</router-link></div>
            <div class="loginIn f26 cfff text-center" @click="getRegistrationId">登录</div>
            <div style="padding:10px 0;"></div>

        </div>
        <div class="faceLogin text-center f26 c999" v-if="show" @click="turnToFace">刷脸登录</div>
    </div>

</template>

<script>
    export default {
        data () {
            return {
                phone:'',
                passWord: '',
                show:true,
                login:{
                    phone:'13244817979',
                    password:'123456'
                }
            }
        },
        methods : {
            faceLoginIn(){
                
                alert('登录')
            },
            otherLoginIn(){
                alert('other')
            },
            returns(){
                this.$router.go(-1);//返回上一层
            },
            turnToFace(){
               this.$router.push({path:'/login'})
            },
            shows(){
                this.show = true;
            },
            hide(){
                this.show = false;
            },
            loginIn(){
                if(this.login.phone == ''){
                    this.$toast('请输入手机号');
                    return
                }
                if(this.login.password == ''){
                    this.$toast('请输入密码');
                    return
                }
                this.$toast.loading({
                    duration: 0
                });
                this.$http({url:this.$api.login},this.login).then(data => {
                    this.$toast.clear();
                    if(data.code == 0){
                        localStorage.setItem('userId',data.data.id);
                        localStorage.setItem('zhxy_user',JSON.stringify(data.data));
                        this.$router.push({path:'/indexs'});
                    }else{
                        this.$toast(data.msg);
                    }
                }).catch( res => {
                    this.$toast.clear();
                    this.$toast(res);
                })
            },
            faceRecognition() {
                alert('触发');
                var that = this;
                Cordova.exec(that.successFunction, that.failFunction, "Plugin", "faceRecognition", ['2','1','2','1']);//type 1入炉2识别 id ,2老师，1登录识别
            },
            successFunction(result){
                alert("successFunction")
            },
            failFunction(msg){
                alert("failFunction")
            },
            successCallback(msg){
                if(msg == 0){
                    this.$router.push({path:'/indexs'})
                }else{
                    alert('失败');
                }
            },
            getRegistrationId(){//获取手机id
                var that = this;
                Cordova.exec(that.success, that.fail, "SmsPlugin", "getRegistrationId", []);
            },
            success(result){
                localStorage.setItem('registrationId',result);
                this.loginIn();
            },
            fail(result){
                this.$toast(result);
            }
        }
    }
</script>

<style scoped>
    .logo{width: 2rem;height:2rem;display: block;margin: 1.3rem auto 0; border-radius: 50%;}
    .inpDiv{width: 6.9rem;height: 0.9rem;border-radius: 0.9rem;background: #f3f3f3;margin: 0.45rem auto 0;position: relative;overflow: hidden;}
    .mt-145{margin-top: 1.45rem}
    .icon{width: 0.3rem;height: 0.3rem;display: block;position: absolute;top: 0.29rem;left: 0.3rem;z-index: 10;}
    .oinp{border: none;width: 6rem;height: 100%;z-index: 5;position: absolute;top: 0;left: 0;background: #f3f3f3;}
    input::-webkit-input-placeholder{
        color: #ccc;
    }
    input::-moz-placeholder{
        color: #ccc;
    }
    input:-ms-input-placeholder{
        color: #ccc;    
    }
    .lossPsw{width: 6.9rem;margin: 0 auto 0;height: 0.5rem;box-sizing: border-box;}
    .ospan{display: block;float: right;padding: 0.19rem 0}
    .loginIn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to right, #82cbff , #7b7ceb);margin: 0.9rem auto 0;line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff; }
    .faceLogin{width: 100%;padding: 0.44rem 0;position: absolute;bottom: 0;z-index: 1}
</style>



